
.App{
    width: 100%;
    height: 100%;
    &__main-page-content{
        // width: 100%;
        height: calc(100% - 80px);
       margin-top: 70px;
    }
}

.home-goto_top{
    position: fixed;
    bottom: 14px;
    
    right: 2.5%;
  }
  .home-goto_top > span{
    position: absolute;
    top: 9px;
    right: 9px;
    height: 52px;
    width: 52px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background: linear-gradient(#1d1d1d,#1976D2,#ffffffe4);
  }
  .home-goto_top > span > img{
  height: 45px;
  width: 45px;
  border-radius: 50%;
  }
  .loader {
    position: relative;
    width: 70px;
    height: 70px;
  }

  
  
  .loader span {
    position: absolute;
    width: 70px;
    height: 70px;
    border-radius: 50%;
  }
  
  .loader span:nth-child(1) {
    filter: blur(5px);
  }
  
  .loader span:nth-child(2) {
    filter: blur(10px);
  }
  
  .loader::after {
    content: "";
    position: absolute;
    top: 10px;
    left: 10px;
    right: 10px;
    bottom: 10px;
    border:  2px solid var(--yellow-theme-main-color);
    border-radius: 50%;
  }
  